<template>
  <div id="screenbox">
    <div id="databox">
      <div>
        <h2>{{detailData.County}}</h2>
      </div>
      <div>
        <h4>:{{detailData.SiteName}}</h4>
      </div>
      <div class="valuebox">
        <div class="datastyle">
          <h4>(AQI)</h4>
          <div class="valuebar">
            <b-progress :max="300" height="13px" class="barwidth">
              <b-progress-bar class="barcolor" :value="datacheck(detailData.AQI)"></b-progress-bar>
            </b-progress>
            <h5>{{detailData.AQI}}</h5>
          </div>
        </div>
        <div class="datastyle">
          <h4 v-b-popover.hover.left="Description.PM10.content" :title="Description.PM10.title">(pm10)</h4>
          <div class="valuebar">
            <b-progress :max="400" height="13px" class="barwidth">
              <b-progress-bar class="barcolor" :value="datacheck(detailData.PM10)"></b-progress-bar>
            </b-progress>
            <h5>{{detailData.PM10}}</h5>
            <p>(ug/m3)</p>
          </div>
        </div>
        <div class="datastyle">
          <h4 v-b-popover.hover.left="Description.PM25.content" :title="Description.PM25.title">(pm2.5)</h4>
          <div class="valuebar">
            <b-progress :max="250" height="13px" class="barwidth">
              <b-progress-bar class="barcolor" :value="datacheck(detailData['PM2.5'])"></b-progress-bar>
            </b-progress>
            <h5>{{detailData['PM2.5']}}</h5>
            <p>(ug/m3)</p>
          </div>
        </div>
        <div class="datastyle">
          <h4 v-b-popover.hover.left="Description.SO2.content" :title="Description.SO2.title">(SO2)</h4>
          <div class="valuebar">
            <b-progress :max="20" height="13px" class="barwidth">
              <b-progress-bar class="barcolor" :value="datacheck(detailData.SO2)"></b-progress-bar>
            </b-progress>
            <h5>{{detailData.SO2}}</h5>
            <p>ppb</p>
          </div>
        </div>
        <div class="datastyle" >
          <h4 v-b-popover.hover.left="Description.CO.content" :title="Description.CO.title">(CO)</h4>
          <div class="valuebar">
            <b-progress :max="1" height="13px" class="barwidth">
              <b-progress-bar class="barcolor" :value="datacheck(detailData.CO)"></b-progress-bar>
            </b-progress>
            <h5>{{detailData.CO}}</h5>
            <p>ppm</p>
          </div>
        </div>
        <div class="datastyle">
          <h4 v-b-popover.hover.left="Description.O3.content" :title="Description.O3.title">(O3)</h4>
          <div class="valuebar">
            <b-progress :max="200" height="13px" class="barwidth">
              <b-progress-bar class="barcolor" :value="datacheck(detailData.O3)"></b-progress-bar>
            </b-progress>
            <h5>{{detailData.O3}}</h5>
            <p>ppb</p>
          </div>
        </div>
        <div class="datastyle" >
          <h4 v-b-popover.hover.left="Description.NO2.content" :title="Description.NO2.title">(NO2)</h4>
          <div class="valuebar">
            <b-progress :max="300" height="13px" class="barwidth">
              <b-progress-bar class="barcolor" :value="datacheck(detailData.NO2)"></b-progress-bar>
            </b-progress>
            <h5>{{detailData.NO2}}</h5>
            <p>ppb</p>
          </div>
        </div>
      </div>
      <button @click="$emit('turnoff')"></button>
    </div>
  </div>
</template>
<script>
export default {
  props: ["detailData"],
  data(){
    return{
      Description:{
        PM25:{
          title:'PM2.5 ',
          content:'PM2.52.5PM2.5PM10'
        },
        PM10:{
          title:'PM10 ',
          content:'1010'
        },
        SO2:{
          title:'SO2 ',
          content:''
        },
        CO:{
          title:'CO ',
          content:''
        },
        O3:{
          title:'O3 ',
          content:''
        },
        NO2:{
          title:'NO2 ',
          content:'NONO2'
        }
      }
    }
  },
  methods: {
    datacheck(data) {
      if (isNaN(data) || data === "") {
        return 0;
      } else {
        return parseFloat(data);
      }
    }
  }
};
</script>
<style lang="scss">
#screenbox {
  position: fixed;
  top: 0;
  //padding: 150px;
  z-index: 15;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  #databox {
    display: flex;
    flex-direction: column;
    border-radius: 8px;
    padding: 8px;
    margin: auto;
    background-color: rgba(0, 131, 192, 0.8);
    color: white;
    margin-top: 50px;
    .datastyle {
      > h4 {
        margin-bottom: 0px;
        text-align: left;
        cursor: pointer;
      }
      .valuebar {
        display: flex;
        margin-bottom: 20px;
        height: 20px;
        align-items: baseline;
        > h5 {
          margin: 0 6px 0 10px;
          font-size: 23px;
        }
        .barwidth {
          width: 350px;
          box-shadow: 0 0px 8px rgb(0, 0, 0);
          .barcolor {
            //box-shadow: 0 0 8px  rgb(0, 0, 0);
            background-color: rgb(253, 190, 17);
          }
        }
      }
    }
    >button{
      width: 80px;
      height: 40px;
      align-self: flex-end;
      margin-right: 10px;
      margin-bottom: 6px;
      border-radius: 8px;
      border: none;
      background-color: rgb(253, 53, 53);
      color: white;
      outline: none;
      transition: 0.2s;
    }
    >button:hover{
      transform: scale(1.1);
    }
    >button:active{
      transform: scale(0.95);
    }
  }
}
</style>

